@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Montserrat", sans-serif}.App .logo{position:absolute;width:48px;height:48px;left:28px;top:28px}.Page{display:flex;flex-direction:column;margin-top:142px;margin-left:137px;margin-right:137px}.Page * h1{font-weight:bold;font-size:24px;line-height:29px;color:#000000}.Page * h2{font-weight:bold;font-size:19px;line-height:23px;color:#000000}.Page * h3{font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;color:#000000}.Page * p{font-weight:normal;font-size:14px;line-height:24px;color:rgba(0,0,0,0.75)}.Page>div{margin:10px 0 10px 0}.text-field{display:inline-flex;width:320px;justify-content:space-between;align-items:center}.text-field p{font-weight:normal;font-size:12px;line-height:15px;text-transform:uppercase;opacity:0.5}

.TextField input{width:320px;height:44px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:12px}.TextField input:focus,.TextField input:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}

.DropDown{display:flex;flex-direction:column;width:320px}.DropDown .Visitors{-webkit-user-select:none;-ms-user-select:none;user-select:none;display:flex;align-items:center;justify-content:space-between;width:320px;height:44px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:14px;padding-right:15px}.DropDown .Visitors img{cursor:pointer}.DropDown .Visitors:focus,.DropDown .Visitors:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}.DropDown .guest-list{-webkit-user-select:none;-ms-user-select:none;user-select:none;height:159px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:10px;padding-right:10px;font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;color:#1f2041}.DropDown .guest-list .guest-list-adults{display:flex;justify-content:space-between;align-items:center}.DropDown .guest-list .guest-list-adults>div{display:flex;align-items:center}.DropDown .guest-list .guest-list-adults>div .minus,.DropDown .guest-list .guest-list-adults>div .plus{display:flex;align-items:center;justify-content:center;width:30px;height:30px;color:#1f2041;cursor:pointer;border:1px solid;border-color:#1f2041;opacity:0.25;border-radius:22px;margin:10px}.DropDown .guest-list .guest-list-adults>div .minus.clicked,.DropDown .guest-list .guest-list-adults>div .plus.clicked{color:#1f2041;border-color:#1f2041;opacity:0.5}

.check-list{display:flex;flex-direction:column}.check-list .checkbox{display:none}.check-list .checkbox+span{display:inline-flex;align-items:flex-start;width:240px;margin:5px 0 5px 0;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-size:14px;color:rgba(31,32,65,0.5)}.check-list .checkbox+span::before{content:"";cursor:pointer;margin-right:10px;width:20px;height:20px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;padding-right:15px}.check-list .checkbox+.checked::before{border:1px solid #bc9cff;box-sizing:border-box;border-radius:4px;background-image:url(/MetaLamp_Task_2/static/media/Vector.ad694aaf.svg);background-repeat:no-repeat;background-position:center}

.RadioButtons{display:flex}.RadioButtons input{display:none}.RadioButtons span{font-weight:normal;color:rgba(31,32,65,0.75);margin-right:15px}.RadioButtons span::before{content:"";cursor:pointer;width:20px;height:20px;padding-right:20px;margin-right:10px;border-radius:50%;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box}.RadioButtons #men:checked+label span::before,.RadioButtons #women:checked+label span::before{border:1px solid #bc9cff;background-image:url(/MetaLamp_Task_2/static/media/circle.8b4bd132.svg);background-repeat:no-repeat;background-position:center}

.Subscription{position:relative;width:320px}.Subscription input{width:267px;height:44px;left:141px;top:163px;background:#ffffff;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:12px}.Subscription input:focus,.Subscription input:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}.arrow-button{position:absolute;padding:0;border:none;font:inherit;color:inherit;width:18px;height:18px;background-color:transparent;background-image:url(/MetaLamp_Task_2/static/media/arrow_forward.ae9e8d9b.svg);background-repeat:no-repeat;cursor:pointer;-webkit-transform:translate(-32px, 13px);transform:translate(-32px, 13px)}

.checkbox{position:relative}.checkbox input{display:none}.checkbox span{display:inline-flex;align-items:flex-start;width:270px;margin:5px 0 5px 0;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-size:14px;color:rgba(31,32,65,0.5)}.checkbox span:before{content:"";cursor:pointer;padding-right:40px;margin-right:10px;width:40px;height:20px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:10px}.checkbox-circle{position:absolute;bottom:9px;left:4px;width:12px;height:12px;background:rgba(31,32,65,0.25);border-radius:10px}.checkbox input:checked ~ span::before{border:1px solid #bc9cff}.checkbox input:checked ~ span .checkbox-circle{-webkit-transform:translateX(22px);transform:translateX(22px);background:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%)}.checkbox *{transition:all 0.5s cubic-bezier(0.2, 0.85, 0.32, 1.2)}

.LikeButton{position:relative}.LikeButton .container{display:inline-block;position:relative;width:40px;height:20px;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:10px;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.LikeButton .container span{position:absolute;top:3px;left:22px;font-style:normal;font-weight:normal;font-size:10px;line-height:12px;color:rgba(31,32,65,0.25)}.LikeButton .container span::before{content:"";position:absolute;top:2px;left:-17px;width:10px;height:8px;transition:0.5s ease-in-out;-webkit-transition:transform 0.5s ease-in-out;-moz-transition:transform 0.5s ease-in-out;-ms-transition:transform 0.5s ease-in-out;-o-transition:transform 0.5s ease-in-out;background-image:url(/MetaLamp_Task_2/static/media/heart.69d1f532.svg)}.LikeButton input{display:none}.LikeButton input:checked ~ .container{border:1px solid #bc9cff}.LikeButton input:checked ~ .container span{color:#bc9cff}.LikeButton input:checked ~ .container span::before{background-image:url(/MetaLamp_Task_2/static/media/heart-act.dabf545f.svg)}

.RateButton{position:relative;width:120px;display:flex;-webkit-transform:rotateY(180deg) translatex(4px);transform:rotateY(180deg) translatex(4px)}.RateButton div{cursor:pointer;width:24px;height:24px;background-image:url(/MetaLamp_Task_2/static/media/star_border.d6f803db.svg);background-repeat:no-repeat}.RateButton .star-1:hover,.RateButton .star-2:hover,.RateButton .star-3:hover,.RateButton .star-4:hover,.RateButton .star-5:hover{background-image:url(/MetaLamp_Task_2/static/media/star.5a1fce0b.svg)}.RateButton .star-1:hover ~ *,.RateButton .star-2:hover ~ *,.RateButton .star-3:hover ~ *,.RateButton .star-4:hover ~ *,.RateButton .star-5:hover ~ *{background-image:url(/MetaLamp_Task_2/static/media/star.5a1fce0b.svg)}.RateButton .active{background-image:url(/MetaLamp_Task_2/static/media/star.5a1fce0b.svg)}

.full,.border,.none,.go-to-pay{border-radius:22px;width:99px;border:none;cursor:pointer;font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;padding:0;height:44px;margin:10px 0 10px 0}.full:hover,.border:hover,.none:hover,.go-to-pay:hover{opacity:1}.full{color:#ffffff;background:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);opacity:0.5}.border{background:white;border:2px solid;color:#bc9cff;border-color:rgba(187,155,255,0.5)}.border:hover{border-color:#bb9bff}.none{color:rgba(31,32,65,0.5);background:white}.none:hover{color:#bc9cff}.go-to-pay{position:relative;width:320px;color:#ffffff;background:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%)}.go-to-pay::after{content:"";position:absolute;width:18px;height:18px;right:22px;background-image:url(/MetaLamp_Task_2/static/media/arrow_forward_white.d4701dff.svg)}

.MaskedTextField{display:flex;flex-direction:column}.MaskedTextField input{cursor:text;position:relative;text-transform:uppercase;width:320px;height:44px;background-color:white;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:4px;font-weight:400;font-size:14px;line-height:18px;color:rgba(31,32,65,0.75);padding-left:12px}.MaskedTextField input:focus,.MaskedTextField input:hover{border:1px solid rgba(31,32,65,0.5);outline-width:0}.MaskedTextField input::after{content:"";position:absolute;z-index:99;top:1px;right:1px;width:30px;height:30px;background-color:white}

.RangeSlider{position:relative}.RangeSlider input{position:absolute;border:none;background-color:rgba(255,255,255,0)}


